.about {
	margin-left: 300px;
	padding: 25px;
	@include r(1400) {
		padding: 20px; }
	@include r(768) {
		margin-left: 0;
		padding: 15px; }
	&__inner {
		display: flex;
		flex-wrap: wrap;
		padding: 30px 0;
		background-color: #fff;
		@include r(1400) {
			padding: 20px 0; } }
	&__column {
		flex: 0 0 50%;
		padding: 0 30px;
		&:first-child {
			border-right: 1px solid #e5e5e5;
			@include r(1200) {
				border: none; } }
		@include r(1400) {
			padding: 0 20px; }
		@include r(1200) {
			flex: 0 0 100%; } }
	&__image {
		position: relative;
		padding-top: 42%;
		@include r(480) {
			padding-top: 56.25%; }
		&-inner {
			position: absolute;
			top: 0;
			right: 0;
			bottom: 0;
			left: 0; }
		&-photo {
			width: 100%;
			height: 100%; } }
	.h3 {
		color: #000;
		font-size: 24px;
		font-weight: 100; }
	&__team {
		margin-top: 20px;
		border-bottom: 1px solid #e5e5e5;
		@include r(992) {
			margin-top: 10px; }
		&-info {
			margin: 10px 0 30px;
			color: #a3a3a3;
			font-size: 18px;
			line-height: 30px;
			font-weight: 300;
			@include r(992) {
				margin: 5px 0 15px; }
			@include r(600) {
				font-size: 16px;
				line-height: 24px; }
			@include r(480) {
				font-size: 14px;
				line-height: 20px; } } }
	&__staff {
		padding-top: 30px;
		@include r(992) {
			padding-top: 15px; }
		.h3 {
			padding-bottom: 20px;
			@include r(992) {
				padding-bottom: 10px; } } }
	&__skills {
		margin-bottom: 25px;
		@include r(1200) {
			margin-top: 25px; }
		.h3 {
			padding-bottom: 12px; } }
	&__testimonials {
		.h3 {
			padding-bottom: 15px; } }
	&__review {
		&-block {
			margin-bottom: 17px;
			position: relative;
			width: 100%;
			height: 80px;
			background-color: #1ab5b3; }
		&-photo {
			position: absolute;
			top: 0;
			left: 0;
			width: 80px;
			height: 80px; }
		&-name {
			position: absolute;
			top: 50%;
			left: 102px;
			right: 10px;
			transform: translateY(-50%);
			font-size: 15px;
			font-style: italic; }
		&-text {
			padding-left: 30px;
			font-size: 18px;
			font-weight: 300;
			line-height: 1.67;
			color: #a3a3a3;
			@include r(480) {
				padding-left: 10px; } } }
	&__services {
		margin-top: 48px;
		font-size: 14px;
		font-weight: 300;
		line-height: 2;
		color: #848484;
		@include r(1200) {
			margin-top: 25px; }
		@include r(480) {
			margin-top: 15px; }
		ul {
			padding-left: 15px;
			list-style-type: disc; } } }

.staff {
	display: flex;
	margin: -7px;
	flex-wrap: wrap;
	&__item {
		flex: 0 0 33.33%;
		padding: 7px;
		cursor: pointer;
		overflow: hidden;
		&:hover {
			.staff__item-info {
				transform: translateX(0); } }
		@include r(1400) {
			flex: 0 0 50%; }
		@include r(1200) {
			flex: 0 0 33.33%; }
		@include r(992) {
			flex: 0 0 50%; }
		@include r(768) {
			flex: 0 0 33.33%; }
		@include r(600) {
			flex: 0 0 50%; }
		@include r(480) {
			flex: 0 0 100%; }
		&-outer {
			position: relative;
			padding-top: 79.3%;
			@include r(480) {
				padding-top: 56.25%; } }
		&-inner {
			position: absolute;
			top: 0;
			right: 0;
			bottom: 0;
			left: 0; }
		&-image {
			width: 100%;
			height: 100%; }
		&-info {
			display: flex;
			flex-direction: column;
			justify-content: center;
			align-items: center;
			position: absolute;
			width: 100%;
			height: 100%;
			background-color: rgba($color_main, 0.9);
			transform: translateX(-110%);
			transition: transform 0.15s ease-out;
			.h4 {
				font-size: 16px;
				font-weight: 700; }
			p {
				font-size: 13px;
				font-weight: 100; } } } }

.skill {
	position: relative;
	margin-bottom: 6px;
	font-weight: 300;
	font-size: 15px;
	color: $color_skills;
	&__percent {
		position: absolute;
		right: -7px;
		font-size: 14px;
		color: #000; } }

.skillbar {
	margin-bottom: 14px;
	width: 100%;
	background-color: #fff;
	border-top: 1px solid #e2e2e2;
	border-bottom: 1px solid #e2e2e2;
	&__inner {
		height: 32px;
		background-color: #e2e2e2; }
	&-1 {
		width: 89%; }
	&-2 {
		width: 97%; }
	&-3 {
		width: 85%; }
	&-4 {
		width: 75%; } }
